import React from 'react'

const Color = ({color,changeColor})=>{
  const style={
    display: "inline-flex",
    marginLeft:"10px",
    height: "30px",
    width: "30px",
    backgroundColor: color,
    borderRadius: "150px",
  }
  return(
    <>
    <div className="round" style={style}
    onClick={()=>{changeColor(color)}}
    >
    </div>
    </>
  )
}

export default Color