import React, { useState } from 'react'

const Btf = ({active,bColor,setActive,setMsg})=>{
  const [style,setStyle]=useState('')

  const act = active==='bColor'?true:false
  const unActiveStyle='mtf col-md-4 col-lg-4 col-xs-8 col-sm-8'
  const activeStyle='mtf-clicked col-md-4 col-lg-4 col-xs-8 col-sm-8'


  const changeStyle = ()=>{
    
    setMsg(false)
    setActive('bColor')
  }
  return(
    <>
      <div className="container">
       <div className="justify-content-center row">
          <div style={{
            backgroundColor:bColor
          }} 
           className={act?activeStyle:unActiveStyle}
           onClick={changeStyle}>
          </div>
       </div>
    </div>
    </>
  )
}

export default Btf