import './App.css';
import '../node_modules/bootstrap/dist/css/bootstrap.min.css'
import Utf from './components/Utf'
import Mtf from './components/Mtf'
import Btf from './components/Btf'
import Htf from './components/Htf'
import Color from './components/Color'
import { useEffect, useState } from 'react';


function App() {
  const colors=['#DCC73D','#cbee68','#ee7a19','#428640','#ec3c3c','#e7f415','#8B3D2A',
  '#f21f1f']
  const [active,setActive]=useState(null)
  const [uColor,setUColor]=useState('#428640')
  const [mColor,setMColor]=useState('#8B3D2A')
  const [bColor,setBColor]=useState('#DCC73D')
  const [msg,setMsg]=useState(false)
  useEffect(()=>{},[uColor,mColor,bColor])
  const changeColor=(color)=>{
      console.log(color,active)
      if(active==='uColor'){
        setUColor(color)
        
      }
      else if (active==='mColor'){
        setMColor(color)
      }
      else if (active==='bColor'){
        setBColor(color)
      }
      else{
        setMsg(true)
      }
  }

  return (
    <>
      <div className="container-fluid">
        <div className="row justify-content-center text-center">
          <div className="mt col-md-8 col-lg-8 col-sm-10 col-xs-10">
            <h4 className="brand-text">
            <span className="tutti">Tutti</span>
            <span className="frutti"> Frutti</span></h4>
            <p className="color-txt">Let's create your own tutti frutti color combination</p>
            <Utf uColor={uColor}  
            active={active}
            setActive={setActive} 
            setMsg={setMsg}/>
            <Mtf mColor={mColor} 
            active={active}
            setActive={setActive}
            setMsg={setMsg}
            />
            <Btf bColor={bColor} 
            active={active}
            setActive={setActive}
            setMsg={setMsg}
            />  
            <Htf/>
            <div className="container my-1">
              <div className="justify-content-center row">
                  <div className="col-md-8 col-lg-8 col-xs-8 col-sm-8">
                    {
                      (msg)&&<p className="m-msg">First click on Tutti Frutti ðŸ˜‹</p>
                    }
                    {
                      colors.map((color,index)=>(
                        <>
                            <Color
                              key={index}
                              color={color}
                              changeColor={changeColor}
                            />
                        </>
                      ))
                    }
                  <p className="made">Made with ðŸ’– by Wasim</p>
                  </div>
              </div>
            </div>

          </div>
        </div>
      </div>
      
    </>
  );
}

export default App;
