import '../App.css';
import React, { useState } from 'react'
import { act } from 'react-dom/test-utils';


const Utf = ({active,uColor,setActive,setMsg})=>{
  
  const act = active==='uColor'?true:false
  const unActiveStyle='utf col-md-4 col-lg-4 col-xs-8 col-sm-8'
  const activeStyle='utf-click col-md-4 col-lg-4 col-xs-8 col-sm-8'
  
  const changeStyle= ()=>{
    setMsg(false)
    setActive('uColor')
    
  }
  return(
    <>
    <div className="container mt-4">
       <div className="justify-content-center row">
          <div style={{
            backgroundColor:uColor
          }} className={act?activeStyle:unActiveStyle}
          onClick={changeStyle}
          >
          
          </div>
       </div>
    </div>
    </>
  )
}
export default Utf