import React, { useState } from 'react'

const Mtf = ({active,mColor,setActive,setMsg})=>{
  
  const act = active==='mColor'?true:false
  const unActiveStyle='mtf col-md-4 col-lg-4 col-xs-8 col-sm-8'
  const activeStyle='mtf-clicked col-md-4 col-lg-4 col-xs-8 col-sm-8'

  const changeStyle = ()=>{
    
    setMsg(false)
    setActive('mColor')
  }
  return(
    <>
      <div className="container">
       <div className="justify-content-center row">
          <div style={{
            backgroundColor:mColor
          }} 
           className={act?activeStyle:unActiveStyle}
           onClick={changeStyle}>
          </div>
       </div>
    </div>
    </>
  )
}

export default Mtf